import React from "react";
import styled from "styled-components";
const Wrapper = styled.div`
  margin-top: 2em;
  margin-left: 6em;
  margin-right: 6em;
`;
const Message = styled.p`
  text-align: center;
  font-size: 60px;
`;
export const NoMatch = () => (
  <Wrapper>
    <Message>404 not Found</Message>
  </Wrapper>
);
