import React, { Component } from 'react'
import NavItem from './NavItem';
import './SideNav.css'
import { Button } from 'primereact/button';
import { Sidebar } from 'primereact/sidebar';
import PrimeReact from 'primereact/utils';

const ButtonStyle = {
  height: '45px',
  width: '45px', /* width must be same size as NavBar to center */
  borderRadius: '30px',
}

class SideNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showRooms: false,
      activePath: props.location.pathname
    }
  }

  onItemClick = (path) => {
    this.setState({ activePath: path }); /* Sets activePath which causes rerender which causes CSS to change */
  }

  render() {
    const { activePath } = this.state;
    PrimeReact.ripple = false;
    return (
      <div className="SideNavStyle">
        <NavItem
          path="/"
          name="Home"
          css='pi pi-home'
          onItemClick={this.onItemClick}
          active={"/" === activePath}
        />
        <br /><br /><br />
        <NavItem
          path="/camera"
          name="Camera"
          css='pi pi-video'
          onItemClick={this.onItemClick}
          active={"/camera" === activePath}
        />
        <NavItem
          path="/channels"
          name="Channels"
          css='pi pi-users'
          onItemClick={this.onItemClick}
          active={"/channels" === activePath}
        />
        <Sidebar
          visible={this.state.showRooms}
          baseZIndex={100}
          modal={false} /* disable dimming */
          onHide={() => this.setState({ showRooms: false })}
          style={{ borderTopRightRadius: '20px', borderBottomRightRadius: '20px', width: '500px', backgroundColor: '#383141' }}
        >
          <h1 style={{ fontWeight: 'normal', color: 'white' }}>Rooms</h1>
        </Sidebar>
        <Button
          className="p-button-rounded p-button-text"
          icon="/"
          onClick={() => this.setState({ showRooms: true })}
          style={{ color: 'grey', margin: '15px', marginBottom: '40px', marginTop: '20px' }}
        >
          <i className="pi pi-comments" style={{ 'fontSize': '2.4em' }}></i>
        </Button>
        <NavItem
          path="/people"
          name="People"
          css='pi pi-users'
          onItemClick={this.onItemClick}
          active={"/people" === activePath}
        />
        <div className="ButtonWrapper">
          <Button icon="/" className="p-button-rounded p-button-text p-button-secondary" style={ButtonStyle}>
            <i className="pi pi-power-off" style={{ 'fontSize': '2.4em' }}></i>
          </Button>
        </div>
      </div>
    );
  }
}

export default SideNav