import React, { Component } from 'react'
import videojs from 'video.js'
import awsvideoconfig from '../aws-video-exports'
import 'video.js/dist/video-js.css'
import styled from 'styled-components'
import { Card } from 'primereact/card';
import { Button } from 'primereact/button';
import './Camera.css';

class VideoPlayer extends React.Component {
  componentDidMount() {
    this.player = videojs(this.videoNode, this.props)
  }

  componentWillUnmount() {
    if (this.player) {
      this.player.dispose()
    }
  }

  render() {
    return (
      <div>
        <div data-vjs-player style={{
          width: '960px',
          height: 540,
        }}>
          <video ref={(node) => { this.videoNode = node; }} className="video-js" />
        </div>
      </div >
    );
  }
}

const videoJsOptions = {
  autoplay: true,
  controls: true,
  sources: [{
    src: awsvideoconfig.awsOutputLiveLL,
  }]
}

const CardStyle = styled.div`
  background-color: grey;
  color: black;
  height: 144px;
  width: 252px;
  margin-bottom: 10px;
  margin-right: 10px;
  border-radius: 10px;
  :hover {
    opacity: 0.7;
  }  
`;

class Camera extends Component {
  render() {
    return (
      <div style={{ display: 'flex' }}>
        <div className='LeftWrapper'>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
          <CardStyle>friends in call</CardStyle>
        </div>
        <div className='VideoPlayerWrapper'>
          <VideoPlayer {...videoJsOptions} />
        </div>
        <div className='RightWrapper'>
          <Button label="Login" className="p-button-rounded" style={{ marginLeft: '80%' }} />
          <br /><br />
          <Card style={{ borderRadius: '10px', minHeight: '92.5%' }}>Messages Messages Messages Messages Messages Messages Messages Messages Messages Messages Messages Messages </Card>
        </div>
      </div >
    )
  }
}

//const nav = { padding: '0px 40px', height: 60, borderBottom: '1px solid #ddd', display: 'flex', alignItems: 'center' }
const container = { paddingTop: 20 }
//const navHeading = { margin: 0, fontSize: 18 }

export default Camera